import React, { Component } from "react";
// import PropTypes from "prop-types";

export class MoviesItem extends Component {
    
  static propTypes = {};

  render() {
      let {title, description, imageurl ,moviesUrl, release, trailerUrl} =this.props;
    return (
      <div>
        <div className="card my-1 rounded-3 border-1 border-info" >
          <img src={imageurl} className="card-img-top img-fluid" alt="..." />
          <div className="card-body">
            <h5 className="card-title text-center">{title}</h5>
            <p className="card-text">
              {description}
            </p>
            <p className="release">
                <strong>Release Date: </strong> {release}
            </p>
            {/* <a rel="noreferrer" href={moviesUrl} target="_blank" className="btn btn-sm btn-primary">
              Merchandise
            </a>
            <a rel="noreferrer" href={moviesUrl} target="_blank" className="btn btn-sm btn-primary">
              View Trailer
            </a> */}
            <div className=" d-flex justify-content-between">
            <a rel="noreferrer" href={moviesUrl} target="_blank" className="btn btn-sm btn-primary mx-1">
              Merchandise
            </a>
            <a rel="noreferrer" href={trailerUrl} target="_blank" className="btn btn-sm btn-primary mx-1">
              View Trailer
            </a>
                </div>
          </div>
        </div>
      </div>
    );
  }
}

export default MoviesItem;
