import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import MoviesItem from './MoviesItem'

export class ActionMovies extends Component {
    articles= [
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Ryan Coogler",
            "title": "Black Panther",
            "description": "After his father's death, T'Challa returns home to Wakanda to inherit his throne. However, a powerful enemy related to his family threatens to attack his nation.",
            "url": "https://www.marvel.com/movies/black-panther",
            "turl": "https://www.youtube.com/watch?v=xjDjIWPwcPU",
            "urlToImage": "https://hips.hearstapps.com/digitalspyuk.cdnds.net/17/42/1508170562-black-panther-poster.jpg",
            "releasedOn": "16 February 2018 (USA)",
            "content": "After the death of his father, T'Challa returns home to the African nation of Wakanda to take his rightful place as king. When a powerful enemy suddenly reappears, T'Challa's mettle as king -- and as Black Panther -- gets tested when he's drawn into a conflict that puts the fate of Wakanda and the entire world at risk. Faced with treachery and danger, the young king must rally his allies and release the full power of Black Panther to defeat his foes and secure the safety of his people."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Anthony Russo; Joe Russo;",
            "title": "Avengers Endgame",
            "description": "After Thanos, an intergalactic warlord, disintegrates half of the universe, the Avengers must reunite and assemble again to reinvigorate their trounced allies and restore balance.",
            "url": "https://www.marvel.com/movies/avengers-endgame",
            "turl": "https://www.youtube.com/watch?v=TcMBFSGVi1c",
            "urlToImage": "https://bgr.com/wp-content/uploads/2019/03/avengers-endgame-poster-top-half.jpg?quality=70&strip=all",
            "releasedOn": " 26 April 2019 (India)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Christopher McQuarrie",
            "title": "Mission:Impossible - Fallout",
            "description": "A group of terrorists plans to detonate three plutonium cores for a simultaneous nuclear attack on different cities. Ethan Hunt, along with his IMF team, sets out to stop the carnage",
            "url": "https://www.paramount.com/movies/mission-impossible-fallout",
            "turl": "https://www.youtube.com/watch?v=wb49-oV0F78",
            "urlToImage": "https://www.avsforum.com/cdn-cgi/image/format=auto,onerror=redirect,width=1920,height=1920,fit=scale-down/https://www.avsforum.com/attachments/mission-impossible-jpg.3021152/",
            "releasedOn": "27 July 2018 (India)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        }
        
    ]
    constructor(){
        super();
        console.log("hello i am a constructor from Movies component");
        this.state={
            articles:this.articles
        }
    }
    static propTypes = {

    }

    render() {
        return (
            <div className="container my-3 justify-content-center">
                <h1 className="head text-center">ACTION MOVIES</h1>
                <div className="row justify-content-center">
                {this.state.articles.map((element)=>{
                    
                    return <div className="col-md-3 mx-3 my-3 " key={element.url} >
                    <MoviesItem  title={element.title} description={element.description?element.description.slice(0,88)+"...":""} imageurl={element.urlToImage} moviesUrl={element.url} release={element.releasedOn} trailerUrl={element.turl}/>

                </div>
                })}
                <div className="container d-flex justify-content-between">
                <button type="button" class="btn btn-dark">&larr; Previous</button>
                <button type="button" class="btn btn-dark">Next  &rarr;</button>
                </div>

               
                
                
                </div>
                
            </div>
        )
    }
}

export default ActionMovies
