import React, { Component } from "react";
import PropTypes from "prop-types";
import MoviesItem from "./MoviesItem";
import { Link } from "react-router-dom";
export class Mood extends Component {
  articles = [
    {
      source: {
        id: "the-washington-post",
        name: "The Washington Post",
      },
      director: "Cristi Puiu",
      title: "The Death of Mister Lazarescu",
      description:
        "Mr. Lazarescu (Ion Fiscuteanu) is a retired Romanian engineer, spending his time in the company of his cats and booze. When he starts feeling unusually ill, he first seeks painkillers from his neighbors. It soon becomes apparent that Lazarescu is indeed sick, and an ambulance arrives with a nurse",
      url: "https://www.netflix.com/in/title/70041148",
      turl: "https://www.youtube.com/watch?v=TyUkn1aMMDs",
      urlToImage:
        "https://agoodmovietowatch.com/wp-content/uploads/Cadru-Death_Mr_Lazarescu-1-660x495.jpeg",
      releasedOn: "22 September 2005 (Romania)",
      content:
        "After the death of his father, T'Challa returns home to the African nation of Wakanda to take his rightful place as king. When a powerful enemy suddenly reappears, T'Challa's mettle as king -- and as Black Panther -- gets tested when he's drawn into a conflict that puts the fate of Wakanda and the entire world at risk. Faced with treachery and danger, the young king must rally his allies and release the full power of Black Panther to defeat his foes and secure the safety of his people.",
    },
    {
      source: {
        id: "the-washington-post",
        name: "The Washington Post",
      },
      director: "Levan Akin",
      title: "And Then We Danced",
      description:
        "A male dancer and his partner have been training for years for a spot in the National Georgian Ensemble. The arrival of another dancer throws him off balance, sparking both an intense rivalry and romantic desire that may cause him to risk his future.",
      url: "https://www.andthenwedancedmovie.com/",
      turl: "https://www.youtube.com/watch?v=n25XEhQ6764",
      urlToImage:
        "https://agoodmovietowatch.com/wp-content/uploads/1302518_andthenwedanced_892429-660x440.jpg",
      releasedOn: " 6 November 2019 (France)",
      content:
        "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame.",
    },
    {
      source: {
        id: "the-washington-post",
        name: "The Washington Post",
      },
      director: "Park Chan-wook",
      title: "Sympathy for Lady Vengeance",
      description:
        "Lee Geum-ja (Lee Young-ae) has spent the last 13 years in prison for a murder she didn't commit. She's fantasized about getting revenge on the various people who wronged her, including the police officer (Nam Il-u) who forced her to confess and a shady teacher (Choi Min-sik) with whom she has a chec",
      url: "https://www.sbs.com.au/movies/movie/sympathy-lady-vengeance",
      turl: "https://www.youtube.com/watch?v=mzXbV9a5GA4",
      urlToImage:
        "https://agoodmovietowatch.com/wp-content/uploads/sympathy_for_page-2000-2000-1125-1125-crop-fill-660x371.jpg",
      releasedOn: "29 July 2005 (South Korea)",
      content:
        "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame.",
    },
  ];
  constructor() {
    super();
    console.log("hello i am a constructor from Movies component");
    this.state = {
      articles: this.articles,
    };
  }
  static propTypes = {};

  render() {
    return (
      <div className="container my-3 ">
        <div className="col-md-12 d-flex justify-content-around my-3">
          <Link to="/Action" className="btn btn-dark">
            Action
          </Link>
          <Link to="/Romantic" className="btn btn-dark">
            Romance
          </Link>
          <Link to="/Horror" className="btn btn-dark">
            Horror
          </Link>
        </div>
        
        <div className="row justify-content-center">
          {this.state.articles.map((element) => {
            return (
              <div className="col-md-3 mx-3 my-3 " key={element.url}>
                <MoviesItem
                  title={element.title}
                  description={
                    element.description
                      ? element.description.slice(0, 88) + "..."
                      : ""
                  }
                  imageurl={element.urlToImage}
                  moviesUrl={element.url}
                  release={element.releasedOn}
                  trailerUrl={element.turl}
                />
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default Mood;
