import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import MoviesItem from './MoviesItem'

export class HorrorMovies extends Component {
    articles= [
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": " Yeon Sang-ho",
            "title": "Train To Busan",
            "description": "Seok-woo and his daughter are on a train to Busan on the latter's birthday to see his wife. However, the journey turns into a nightmare when they are trapped amidst a zombie outbreak in South Korea.",
            "url": "https://www.wellgousa.com/films/train-to-busan",
            "turl": "https://www.youtube.com/watch?v=pyWuHv2-Abk",
            "urlToImage": "https://agoodmovietowatch.com/wp-content/uploads/traintobusan-001.jpg",
            "releasedOn": "July 2016 (South Korea))",
            "content": "After the death of his father, T'Challa returns home to the African nation of Wakanda to take his rightful place as king. When a powerful enemy suddenly reappears, T'Challa's mettle as king -- and as Black Panther -- gets tested when he's drawn into a conflict that puts the fate of Wakanda and the entire world at risk. Faced with treachery and danger, the young king must rally his allies and release the full power of Black Panther to defeat his foes and secure the safety of his people."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Julia Ducournau",
            "title": "Raw, 2017",
            "description": "Stringent vegetarian Justine (Garance Marillier) encounters a decadent, merciless and dangerously seductive world during her first week at veterinary school. Desperate to fit in, she strays from her principles and eats raw meat for the first time. The young woman soon experiences terrible and unexpected consequences as her true self begins to emerge.",
            "url": "https://www.focusfeatures.com/raw",
            "turl": "https://www.youtube.com/watch?v=gFlXVX2af_Y",
            "urlToImage": "https://agoodmovietowatch.com/wp-content/uploads/Screenshot-2020-10-28-at-16.17.37-660x462.jpg",
            "releasedOn": " 15 March 2017 (Belgium)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Taika Waititi, Jemaine Clement",
            "title": "What We Do in The Shadows",
            "description": "Vampire housemates (Jemaine Clement, Taika Waititi, Jonathan Brugh) try to cope with the complexities of modern life and show a newly turned hipster (Cori Gonzalez-Macuer) some of the perks of being undead.",
            "url": "https://twitter.com/theshadowsfx?ref_src=twsrc%5Egoogle%7Ctwcamp%5Eserp%7Ctwgr%5Eauthor",
            "turl": "https://www.youtube.com/watch?v=mfBbSwX6kEk",
            "urlToImage": "https://agoodmovietowatch.com/wp-content/uploads/wwdis.jpg",
            "releasedOn": " 13 February 2015 (USA)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        }
        
    ]
    constructor(){
        super();
        console.log("hello i am a constructor from Movies component");
        this.state={
            articles:this.articles
        }
    }
    static propTypes = {

    }

    render() {
        return (
            <div className="container my-3 justify-content-center">
                <h1 className="head text-center">HORROR MOVIES</h1>
                <div className="row justify-content-center">
                {this.state.articles.map((element)=>{
                    
                    return <div className="col-md-3 mx-3 my-3 " key={element.url} >
                    <MoviesItem  title={element.title} description={element.description?element.description.slice(0,88)+"...":""} imageurl={element.urlToImage} moviesUrl={element.url} release={element.releasedOn} trailerUrl={element.turl}/>

                </div>
                })}
                <div className="container d-flex justify-content-between">
                <button type="button" class="btn btn-dark">&larr; Previous</button>
                <button type="button" class="btn btn-dark">Next  &rarr;</button>
                </div>

               
                
                
                </div>
                
            </div>
        )
    }
}

export default HorrorMovies
