import React, { Component } from "react";
// import PropTypes from "prop-types";

import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";
export class Footer extends Component {
  static propTypes = {};

  render() {
    return (
      <div>
        <MDBFooter color="blue" className="font-small pt-4 mt-4">
          <MDBContainer fluid className="text-center text-md-left">
            <MDBRow>
              <MDBCol md="6">
                <h5 className="title">Movies according to your mood</h5>
                <p>
                  Here you can get all the best movies according to your mood.. 
                  You also get the full details and honest review of the selected movie 
                </p>
              </MDBCol>
              <MDBCol md="6">
                <h5 className="title">Contact Me</h5>
                <ul>
                  <li className="list-unstyled">
                    <a href="https://www.instagram.com/a_i_m_aditya/" target="_blank bg-dark">
                        {/* insta */}
                        <img src="https://i.imgur.com/YyYLDLU.jpg" alt="insta" />
                    </a>
                    </li>
                  {/* 
                  <li className="list-unstyled">
                    <a href="#!">Link 2</a>
                  </li>
                  <li className="list-unstyled">
                    <a href="#!">Link 3</a>
                  </li>
                  <li className="list-unstyled">
                    <a href="#!">Link 4</a>
                  </li> */}
                </ul>
              </MDBCol>
            </MDBRow>
          </MDBContainer>
          <div className="footer-copyright text-center py-3 bg-dark text-light">
            <MDBContainer fluid>
              &copy; {new Date().getFullYear()} Copyright:{" "}
              <a href="https://www.mdbootstrap.com">goodmovies.com</a>
            </MDBContainer>
          </div>
        </MDBFooter>
      </div>
    );
  }
}

export default Footer;
