import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import MoviesItem from './MoviesItem'

export class RomanticMovies extends Component {
    articles= [
        {
            "source": { 
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Guillermo del Toro",
            "title": "The Shape Of Water",
            "description": "Elisa, a lonely janitor, stumbles upon an amphibious creature that is held captive in a secret research facility. She then develops a unique relationship with the creature.",
            "url": "https://www.20thcenturystudios.com/movies/the-shape-of-water",
            "turl": "https://www.youtube.com/watch?v=XFYWazblaUA",
            "urlToImage": "https://i.insider.com/5a96cf9baae6051d008b462a?width=1000&format=jpeg&auto=webp",
            "releasedOn": "1 December 2017 (USA)",
            "content": "After the death of his father, T'Challa returns home to the African nation of Wakanda to take his rightful place as king. When a powerful enemy suddenly reappears, T'Challa's mettle as king -- and as Black Panther -- gets tested when he's drawn into a conflict that puts the fate of Wakanda and the entire world at risk. Faced with treachery and danger, the young king must rally his allies and release the full power of Black Panther to defeat his foes and secure the safety of his people."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Michael Showalter",
            "title": "The Big Sick",
            "description": "Kumail is a Pakistani comic, who meets an American graduate student named Emily at one of his stand-up shows. As their relationship blossoms, he soon becomes worried about what his traditional Muslim parents will think of her. When Emily suddenly comes down with an illness that leaves her in a coma,… ",
            "url": "https://www.marvel.com/movies/avengers-endgame",
            "turl": "https://www.youtube.com/watch?v=PJmpSMRQhhs",
            "urlToImage": "https://film-grab.com/wp-content/uploads/2019/03/bigsick015.jpg",
            "releasedOn": " 14 July 2017 (USA)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        },
        {
            "source": {
                "id": "the-washington-post",
                "name": "The Washington Post"
            },
            "director": "Paweł Pawlikowski",
            "title": "Cold war",
            "description": "Set against the backdrop of the 1950s Cold War in Poland, two people of differing backgrounds and temperaments begin an almost impossible romance.",
            "url": "https://www.paramount.com/movies/mission-impossible-fallout",
            "turl": "https://www.youtube.com/watch?v=8ImvkXgGVWw",
            "urlToImage": "https://agoodmovietowatch.com/wp-content/uploads/d08cf2ae91bb15ff35dc5b2173bde878-e1554037014205.jpg",
            "releasedOn": " 8 June 2018 (Poland)",
            "content": "The grave course of events set in motion by Thanos that wiped out half the universe and fractured the Avengers ranks compels the remaining Avengers to take one final stand in Marvel Studios' grand conclusion to twenty-two films, Avengers: Endgame."
        }
        
    ]
    constructor(){
        super();
        console.log("hello i am a constructor from Movies component");
        this.state={
            articles:this.articles
        }
    }
    static propTypes = {

    }

    render() {
        return (
            <div className="container my-3 justify-content-center">
                <h1 className="head text-center">ROMANTIC MOVIES</h1>
                <div className="row justify-content-center">
                {this.state.articles.map((element)=>{
                    
                    return <div className="col-md-3  my-3 " key={element.url} >
                    <MoviesItem  title={element.title} description={element.description?element.description.slice(0,88)+"...":""} imageurl={element.urlToImage} moviesUrl={element.url} release={element.releasedOn} trailerUrl={element.turl}/>

                </div>
                })}
                <div className="container d-flex justify-content-between">
                <button type="button" className="btn btn-dark">&larr; Previous</button>
                <button type="button" className="btn btn-dark">Next  &rarr;</button>
                </div>

               
                
                
                </div>
                
            </div>
        )
    }
}

export default RomanticMovies
